* {
    box-sizing: border-box;
    margin: 0;
}

body {
    font-family: "Roboto";
}

h1,
h2,
h3,
h4,
h5,
h6,
ul,
p,
button {
    margin: 0;
    padding: 0;
}

a{
    text-decoration: none;
    color:unset;
}

li{
    list-style: none;
}

.container {
    max-width: 1440px;
    margin: 0 auto;
}